.history {
	padding: 60px 20px 15px 40px;
	&__container {
		padding: 0; }
	&__title span {
		padding: 0 96px; }
	&__article {
		margin: 60px 0 0;
		color: $grey;
		&-top {
			display: flex;
			align-items: flex-start;
			margin-bottom: 30px; }
		&-title {
			margin-bottom: 25px;
			padding-right: 190px;
			@include f('con', 24); }
		&-img {
			position: relative;
			flex: 0 1 465px;
			&::before, &::after {
				content: '';
				position: absolute;
				@include s('horiz-decor-xs');
				left: calc(50% - 88px); }
			&::before {
				top: -15px; }
			&::after {
				bottom: -15px;
				@include rotate(180); }
			&-decor {
				&::before, &::after {
					content: '';
					position: absolute;
					@include s('horiz-decor-xs_l');
					top: calc(50% - 88px); }
				&::before {
					left: -15px; }
				&::after {
					right: -15px;
					@include rotate(180); } } }
		&-text {
			flex: 1 1 830px;
			margin-left: 42px; }
		&-descr {
			line-height: 22px; }
		&-sub-title {
			margin: 15px 0;
			@include f('con', 16); } } }
