.container {
	margin: 0 auto;
	padding: 0 15px; }

.main {
	position: relative;
	min-height: 100vh;
	margin-left: 400px;
	overflow-y: auto;
	overflow-x: hidden;
	@include scrollbar {
		width: 8px;
		box-shadow: inset 0 0 6px rgba($crimson, .5);
		background-color: $beige; }
	@include scrollbar-thumb {
		background-color: $crimson; }

	&_home {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 400px;
		margin-left: 0; }

	&_has-decor {
		background: url("../img/bg/bg-pattern.png"); } }
